// Core
import React from "react";
import { useFormik } from "formik";

// UI
import { TextField, Grid, Box } from "@material-ui/core";
import DialogActions from "@material-ui/core/DialogActions";
import FormDialog from "components/FormDialog";
import UserMultiSelect from "../UserMultiSelect";
import TypeSelector from "../TypeSelector";
import DialogActionButton from "components/DialogActionButton";
// Styles
import { StyledGridContainer } from "./styles";

// Utils
import { getInitialFormValues } from "utils/projectUtils";

// Constants

export default function LogForm({
    open,
    onClose,
    userList,
    projectTypes,
    handleSubmit,
    initialData,
    handleDeleteProject,
    onGetProjectUserList,
}) {
    const initialValues = getInitialFormValues(initialData);

    const formik = useFormik({
        initialValues,
        onSubmit: data => {
            handleSubmit(data, initialData?.id);
        },
    });

    return (
        <FormDialog open={open} handleClose={onClose} title="Project Item:">
            <StyledGridContainer>
                <Grid container>
                    <Grid item>
                        <Box mr={1}>
                            <TextField
                                required
                                id="name"
                                label="Project name"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.name}
                            />
                        </Box>
                    </Grid>
                    <Grid item>
                        <Box ml={1}>
                            <TextField
                                required
                                id="alias"
                                label="Alias"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.alias}
                            />
                        </Box>
                    </Grid>
                </Grid>
                <Grid item xs>
                    <TextField
                        required
                        id="descriptions"
                        defaultValue={formik.values.descriptions}
                        onChange={formik.handleChange}
                        label="Description"
                        multiline
                        fullWidth
                        rows={4}
                        variant="outlined"
                    />
                </Grid>
                <Grid item xs={12}>
                    <UserMultiSelect
                        onChange={formik.setFieldValue}
                        onGetProjectUserList={onGetProjectUserList}
                        values={formik.values.users}
                        userList={userList}
                        id={"users"}
                    />
                </Grid>
                <Grid container justify="flex-end">
                    <TypeSelector
                        value={formik.values.type}
                        projectTypes={projectTypes}
                        onChange={formik.handleChange}
                    />
                </Grid>
                <DialogActions>
                    {initialData && (
                        <DialogActionButton
                            onClick={() => handleDeleteProject(initialData?.id)}
                            type="error"
                        >
                            Delete
                        </DialogActionButton>
                    )}
                    <DialogActionButton onClick={onClose} type="default">
                        Cancel
                    </DialogActionButton>
                    <DialogActionButton
                        onClick={formik.handleSubmit}
                        type="success"
                    >
                        Save
                    </DialogActionButton>
                </DialogActions>
            </StyledGridContainer>
        </FormDialog>
    );
}
