// Core
import React, { useEffect, useState } from "react";
import moment from "moment";

// UI
import { Box, Typography } from "@material-ui/core";
import { DAY_FORMAT, FULL_DATE_FORMAT } from "constants/dateFormats";

// Styles
import { StyledButton } from "./styles";

// Utils
import { getMonthNameByWeek } from "utils/developerReportUtils";

function checkLogDate(logs, date) {
    return logs.some(item =>
        moment(item.date, FULL_DATE_FORMAT).isSame(
            moment(date, FULL_DATE_FORMAT),
            "day"
        )
    );
}

export default function ({ selectedWeek, logs, setDay, selectedDay }) {
    const [weekDates, setWeekDates] = useState([]);

    useEffect(() => {
        if (!selectedWeek) {
            setWeekDates([]);
            return;
        }
        const getWeekDates = weekNumber => {
            return Array.from({ length: 7 }).map((_, index) =>
                moment()
                    .isoWeek(weekNumber)
                    .isoWeekday(index + 1)
            );
        };

        setWeekDates(getWeekDates(selectedWeek));
    }, [selectedWeek]);

    const formatDate = day => moment(day).format(DAY_FORMAT);
    const isActive = day => moment(day).isSame(selectedDay, "day");
    return (
        selectedWeek && (
            <Box display="flex" flexWrap="wrap">
                {weekDates.map(day => {
                    const hasLogs = checkLogDate(logs, day);
                    return (
                        <StyledButton
                            size="large"
                            onClick={() => setDay(day)}
                            isActive={isActive(day)}
                            hasLogs={hasLogs}
                            variant="contained"
                            key={`date-${day}`}
                        >
                            {formatDate(day)}
                        </StyledButton>
                    );
                })}
                <Box width="100%" ml={1}>
                    <Typography variant="subtitle1">
                        {getMonthNameByWeek(selectedWeek)}
                    </Typography>
                </Box>
            </Box>
        )
    );
}
