// Core
import React from "react";
import { useFormik } from "formik";

// UI
import { Grid, TextField, Box } from "@material-ui/core";
import DialogActions from "@material-ui/core/DialogActions";
import FormDialog from "components/FormDialog";
import SkillMultiSelect from "../SkillsSelector";
import RateInput from "../RateInput";
import DialogActionButton from "components/DialogActionButton";
// Styles
import { StyledGridContainer } from "./styles";

// Utils
import { getInitialFormValues } from "utils/developersUtils";
import RoleSelector from "../RoleSelector";

export default function CreateUserForm({
    open,
    onClose,
    handleSubmit,
    initialData,
    handleDeleteUser,
}) {
    const initialValues = getInitialFormValues(initialData);

    const formik = useFormik({
        initialValues,
        onSubmit: data => {
            handleSubmit(data, initialData?.id);
        },
    });

    return (
        <FormDialog open={open} handleClose={onClose} title="Project Item:">
            <form autoComplete="off">
                <StyledGridContainer>
                    <Grid container>
                        <Box mr={1}>
                            <TextField
                                required
                                id="firstname"
                                label="First name"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.firstname}
                                autoComplete="off"
                            />
                        </Box>
                        <Box ml={1}>
                            <TextField
                                required
                                id="lastname"
                                label="Last name"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.lastname}
                                autoComplete="off"
                            />
                        </Box>
                    </Grid>
                    <Grid item xs={12}>
                        <SkillMultiSelect
                            values={formik.values.skills}
                            onChange={formik.setFieldValue}
                            id="skills"
                        />
                    </Grid>
                    <Grid container justify={"space-between"}>
                        <Grid item xs={8}>
                            <TextField
                                id="country"
                                label="Country"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.country}
                            />
                        </Grid>
                        <Grid item xs={2}>
                            <RateInput
                                onChange={formik.setFieldValue}
                                id="rate"
                                value={formik.values.rate}
                            />
                        </Grid>
                    </Grid>
                    <Grid item xs>
                        <TextField
                            required
                            id="email"
                            label="e-mail"
                            onChange={formik.handleChange}
                            defaultValue={formik.values.email}
                        />
                    </Grid>
                    <Grid container justify="flex-end">
                        <RoleSelector
                            value={formik.values.role}
                            onChange={formik.setFieldValue}
                            id="role"
                        />
                    </Grid>
                    <DialogActions>
                        {initialData && (
                            <DialogActionButton
                                onClick={() =>
                                    handleDeleteUser(initialData?.id)
                                }
                                type="error"
                            >
                                Delete
                            </DialogActionButton>
                        )}
                        <DialogActionButton onClick={onClose} type="default">
                            Cancel
                        </DialogActionButton>
                        <DialogActionButton
                            onClick={formik.handleSubmit}
                            type="success"
                        >
                            Save
                        </DialogActionButton>
                    </DialogActions>
                </StyledGridContainer>
            </form>
        </FormDialog>
    );
}
