// Core
import React, { useEffect, useState } from "react";
import { useObserver } from "mobx-react";
import moment from "moment";
// UI
import { Container, Grid, Box } from "@material-ui/core";
import AddButton from "components/AddButton";
import WeekSelector from "components/WeekSelector";
import WeekDates from "components/WeekDates";
import ReportTable from "Pages/developer-reports/ReportTable";
import ReportForm from "Pages/developer-reports/ReportForm";
import TotalTimeInfo from "./TotalTimeInfo";

// Utils
import { useStores } from "../../store";
import {
    getParametersForLogs,
    getStartOfWeek,
} from "utils/developerReportUtils";

function useStore() {
    const store = useStores();

    return useObserver(() => ({
        logs: store.logStore.logs,
        logTypes: store.logStore.logTypes,
        projectList: store.projectStore.projectList,
        onGetLogs: store.logStore.onGetLogs,
        onGetLogTypes: store.logStore.onGetLogTypes,
        onGetProjectList: store.projectStore.onGetProjectList,
        onAddLog: store.logStore.onAddLog,
        onUpdateLog: store.logStore.onUpdateLog,
        onDeleteLog: store.logStore.onDeleteLog,
        setEmptyLogs: store.logStore.setEmptyLogs,
    }));
}

export default function Developers() {
    const {
        logs,
        logTypes,
        projectList,
        onGetLogs,
        onGetProjectList,
        onGetLogTypes,
        onAddLog,
        onUpdateLog,
        onDeleteLog,
        setEmptyLogs,
    } = useStore();

    const [selectedWeek, setWeek] = useState(moment().isoWeek());
    const [selectedDay, setDay] = useState();
    const [isModalVisible, setModalVisibility] = useState(false);
    const [editingLog, setEditingLog] = useState();
    const [totalWeekTime, setTotalWeekTime] = useState();
    const [totalDayTime, setTotalDayTime] = useState();

    useEffect(() => {
        if (selectedWeek) {
            setDay(getStartOfWeek(selectedWeek));
        }
    }, [selectedWeek]);

    useEffect(() => {
        if (!selectedWeek) {
            setEmptyLogs();
            return;
        }
        onGetLogs(getParametersForLogs(selectedWeek));
    }, [onGetLogs, selectedWeek, setEmptyLogs]);

    useEffect(() => {
        onGetProjectList();
        onGetLogTypes({
            params: {
                page: 1,
            },
        });
    }, [onGetLogTypes, onGetProjectList]);

    const showModal = () => {
        setModalVisibility(true);
    };

    const closeModal = () => {
        setModalVisibility(false);
        setEditingLog(null);
    };

    const handleEditLog = log => {
        setEditingLog(log);
        showModal();
    };

    const handleSubmitForm = async (data, id) => {
        editingLog ? await onUpdateLog(data, id) : await onAddLog(data);
        closeModal();
    };

    const handleDeleteLog = async id => {
        closeModal();
        await onDeleteLog(id);
        await onGetLogs(getParametersForLogs(selectedWeek));
    };

    return (
        <Container>
            <Grid container wrap="wrap">
                <Box width="10%" m={2} minWidth={120}>
                    <WeekSelector
                        setWeek={setWeek}
                        selectedWeek={selectedWeek}
                    />
                </Box>
                <Box m={2}>
                    <WeekDates
                        selectedWeek={selectedWeek}
                        selectedDay={selectedDay}
                        setDay={setDay}
                        logs={logs}
                    />
                </Box>
                <Box
                    m={2}
                    display="flex"
                    justifyContent="space-between"
                    width="100%"
                    flexWrap="wrap"
                >
                    <Box>
                        <AddButton handleClick={showModal}>
                            Add work item
                        </AddButton>
                    </Box>
                    <Box
                        display="flex"
                        flexDirection="column"
                        alignItems="flex-end"
                    >
                        <TotalTimeInfo
                            totalDayTime={totalDayTime}
                            totalWeekTime={totalWeekTime}
                            selectedWeek={selectedWeek}
                        />
                    </Box>
                </Box>
                <Grid item xs={12}></Grid>
                <ReportTable
                    logList={logs}
                    projects={projectList}
                    logTypes={logTypes}
                    handleEditLog={handleEditLog}
                    selectedDay={selectedDay}
                    selectedWeek={selectedWeek}
                    setTotalWeekTime={setTotalWeekTime}
                    setTotalDayTime={setTotalDayTime}
                />

                {isModalVisible && (
                    <ReportForm
                        open={isModalVisible}
                        handleClose={closeModal}
                        projects={projectList}
                        logTypes={logTypes}
                        submitForm={handleSubmitForm}
                        deleteLog={handleDeleteLog}
                        initialData={editingLog}
                        selectedDay={selectedDay}
                        selectedWeek={selectedWeek}
                    />
                )}
            </Grid>
        </Container>
    );
}
