// Core
import React from "react";
import moment from "moment";
import { useFormik } from "formik";

// UI
import { TextField, Grid } from "@material-ui/core";
import DialogActions from "@material-ui/core/DialogActions";
import TimePicker from "components/TimePicker";
import FormDialog from "components/FormDialog";
import DialogActionButton from "components/DialogActionButton";
import Box from "@material-ui/core/Box";
import Selector from "../Selector";
import LogInfoHeader from "../LogInfoHeader";

// Styles
import useStyles from "./styles";

// Utils
import { getInitialFormValues } from "utils/developerReportUtils";

// Constants
import { validationErrorMessages } from "constants/developerReport";
import { FULL_DATE_FORMAT, TIME_FORMAT } from "constants/dateFormats";

function validateTimeRange(from, to) {
    console.log("from: ", moment(from, TIME_FORMAT).format(TIME_FORMAT));
    console.log("to: ", moment(to, TIME_FORMAT).format(TIME_FORMAT));
    console.log(
        "validate ",
        moment(from, TIME_FORMAT).isBefore(moment(to, TIME_FORMAT))
    );
    return moment(from, TIME_FORMAT).isBefore(moment(to, TIME_FORMAT));
}

function validate(values, props) {
    console.log("props: ", props);
    console.log("values: ", values);
    const errors = {};
    if (!validateTimeRange(values.from_time, values.to_time)) {
        errors.from_time = validationErrorMessages.timeInterval;
        errors.to_time = validationErrorMessages.timeInterval;
    }
    console.log("errors: ", errors);
    return errors;
}

export default function LogForm({
    open,
    handleClose,
    projects,
    logTypes,
    submitForm,
    initialData,
    deleteLog,
    selectedDay,
    selectedWeek,
}) {
    const classes = useStyles();

    const initialValues = getInitialFormValues(initialData);

    if (!initialData) {
        initialValues.date = moment(selectedDay, FULL_DATE_FORMAT).format(
            FULL_DATE_FORMAT
        );
    }

    const formik = useFormik({
        initialValues,
        validate,
        validateOnChange: true,
        validateOnBlur: false,
        onSubmit: data => {
            submitForm(getInitialFormValues(data), initialData?.id);
        },
    });

    console.log("formik: ", formik.touched);
    const handleDeleteLog = async () => {
        await deleteLog(initialData.id);
        handleClose();
    };

    const onSetTouched = id => {
        formik.setTouched({
            ...formik.touched,
            [id]: true,
        });
    };

    const workingDay = moment(formik.values.date, FULL_DATE_FORMAT).format(
        "DD MMMM"
    );

    return (
        <FormDialog open={open} handleClose={handleClose} title="Work Item:">
            <LogInfoHeader
                workingDay={workingDay}
                selectedWeek={selectedWeek}
            />
            <form autoComplete="off">
                <Grid container className={classes.root}>
                    <Grid item xs={12} sm={12}>
                        <Grid container wrap="nowrap">
                            <Box mr={1}>
                                <TimePicker
                                    id="from_time"
                                    label="Start time *"
                                    error={
                                        formik.touched.to_time &&
                                        formik.touched.from_time &&
                                        formik.errors.from_time
                                    }
                                    value={formik.values["from_time"]}
                                    onChange={formik.setFieldValue}
                                    setTouched={onSetTouched}
                                />
                            </Box>
                            <Box ml={1}>
                                <TimePicker
                                    margin="normal"
                                    id="to_time"
                                    error={
                                        formik.touched.to_time &&
                                        formik.touched.from_time &&
                                        formik.errors.to_time
                                    }
                                    label="End time *"
                                    value={formik.values["to_time"]}
                                    onChange={formik.setFieldValue}
                                    setTouched={onSetTouched}
                                />
                            </Box>
                        </Grid>
                    </Grid>
                    <Grid container justify="space-between">
                        <Selector
                            data={logTypes}
                            placeholder="Log type *"
                            labelKey="type"
                            valueKey="id"
                            onChange={formik.setFieldValue}
                            id="log_type_id"
                            value={formik.values["log_type_id"]}
                        />
                        <Selector
                            placeholder="Project name *"
                            data={projects}
                            labelKey="name"
                            valueKey="id"
                            onChange={formik.setFieldValue}
                            id="project_id"
                            value={formik.values["project_id"]}
                        />
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="descriptions"
                        fullWidth
                        defaultValue={formik.values.descriptions}
                        onChange={formik.handleChange}
                        label="Description"
                        multiline
                        rows={4}
                        variant="outlined"
                    />
                </Grid>
                <DialogActions>
                    {initialData && (
                        <DialogActionButton
                            onClick={handleDeleteLog}
                            type="error"
                        >
                            Delete
                        </DialogActionButton>
                    )}
                    <DialogActionButton onClick={handleClose} type="default">
                        Cancel
                    </DialogActionButton>
                    <DialogActionButton
                        onClick={formik.handleSubmit}
                        type="success"
                    >
                        Save
                    </DialogActionButton>
                </DialogActions>
            </form>
        </FormDialog>
    );
}
