// Core
import React, { useEffect, useState } from "react";

// UI
import {
    TableContainer,
    TableHead,
    TableBody,
    Paper,
    Table,
    Box,
} from "@material-ui/core";
import TableRow from "components/TableRow";
import LogStatusHandler from "../LogStatusHandler";
import DownloadButton from "components/DownloadButton";
import TableCell from "components/TableCell";

// Constants
import { TABLE_HEADERS } from "constants/managerReports";

// Utils
import { getLogsParams, formatTimeDuration } from "utils/managerReportsUtils";

export default function LogReportTable({
    logList,
    onGetLogs,
    fromDate,
    toDate,
    onUpdateLogStatus,
    userList,
    projectList,
    selectedProjects,
    selectedUsers,
    onDownloadFile,
}) {
    const [filteredLogList, setFilteredLogList] = useState(logList);

    useEffect(() => {
        let result = logList;
        if (selectedProjects.length) {
            const projectsSet = new Set(selectedProjects.map(item => item.id));
            result = logList.filter(log => projectsSet.has(log.project_id));
        }
        if (selectedUsers.length) {
            const usersSet = new Set(selectedUsers.map(item => item.id));
            result = result.filter(log => usersSet.has(log.user_id));
        }
        return setFilteredLogList(result);
    }, [logList, selectedProjects, selectedUsers]);

    useEffect(() => {
        if (fromDate && toDate) {
            onGetLogs(getLogsParams(fromDate, toDate));
        }
    }, [fromDate, onGetLogs, toDate]);

    const getProjectName = id => {
        return projectList.find(project => project.id === id)?.name;
    };

    const getUserName = id => {
        const userData = userList.find(user => user.id === id);
        return `${userData?.firstname} ${userData?.lastname}`;
    };

    return (
        <>
            <TableContainer component={Paper}>
                <Table aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            {TABLE_HEADERS.map(item => (
                                <TableCell key={item}>{item}</TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {filteredLogList.length ? (
                            filteredLogList.map(log => (
                                <TableRow hover key={`log-${log.id}`}>
                                    <TableCell noWrap>{log.date}</TableCell>
                                    <TableCell>
                                        {getUserName(log.user_id)}
                                    </TableCell>
                                    <TableCell>
                                        {getProjectName(log.project_id)}
                                    </TableCell>
                                    <TableCell>{log.descriptions}</TableCell>
                                    <TableCell noWrap>
                                        {formatTimeDuration(log.total)}
                                    </TableCell>
                                    <TableCell>{""}</TableCell>
                                    <TableCell>
                                        <LogStatusHandler
                                            onUpdateLogStatus={
                                                onUpdateLogStatus
                                            }
                                            status={log.status}
                                            id={log.id}
                                        />
                                    </TableCell>
                                </TableRow>
                            ))
                        ) : (
                            <TableRow>
                                <Box p={3} component="td">
                                    {"Empty"}
                                </Box>
                            </TableRow>
                        )}
                    </TableBody>
                </Table>
            </TableContainer>
            <Box display="flex" justifyContent="flex-end" my={3}>
                {filteredLogList.length > 0 && (
                    <DownloadButton handleClick={onDownloadFile}>
                        Export
                    </DownloadButton>
                )}
            </Box>
        </>
    );
}
