// Core
import React from "react";

// UI
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import ProfileMenu from "components/ProfileMenu";
// Components
import NavigationMenu from "./NavigationMenu";

// Styles
import { StyledLink } from "./styles";

// UI
import Logo from "components/Logo";

// Utils
import { getRoutesByRole } from "../../utils/routerUtils";

// Hooks
const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
}));

const NavigationPanel = ({ me, onLogout }) => {
    const classes = useStyles();

    const routes = getRoutesByRole(me?.role);

    return (
        <AppBar position="static">
            <Toolbar>
                <Logo />

                <Typography variant="h6" className={classes.title} />

                {me ? (
                    <ProfileMenu me={me} onLogout={onLogout} />
                ) : (
                    <Button>
                        <StyledLink to="/signin">Login</StyledLink>
                    </Button>
                )}
                <NavigationMenu menuItems={routes} />
            </Toolbar>
        </AppBar>
    );
};

export default NavigationPanel;
