// Core
import React from "react";
import { Container, Box, Typography } from "@material-ui/core";
import ReportHistoryTable from "./ReportHistoryTable";
import { useStores } from "store";
import { useObserver } from "mobx-react";

function useStore() {
    const { exportStore, loadersStore, userStore, projectStore } = useStores();

    return useObserver(() => ({
        historyItems: exportStore.historyItems,
        onGetHistoryItems: exportStore.onGetHistoryItems,
        setLoadingPage: loadersStore.setLoadingPage,
        projectList: projectStore.projectList,
        onGetProjectList: projectStore.onGetProjectList,
        userList: userStore.userList,
        onGetUserList: userStore.onGetUserList,
    }));
}

export default function ReportHistory() {
    const {
        onGetHistoryItems,
        historyItems,
        setLoadingPage,
        onGetProjectList,
        projectList,
        onGetUserList,
        userList,
    } = useStore();

    React.useEffect(() => {
        const loadData = async () => {
            setLoadingPage(true);
            await onGetProjectList();
            setLoadingPage(false);
        };
        if (!projectList.length) {
            loadData();
        }
    }, [onGetProjectList, projectList.length, setLoadingPage]);

    React.useEffect(() => {
        const loadData = async () => {
            setLoadingPage(true);
            await onGetUserList();
            setLoadingPage(false);
        };
        if (!userList.length) {
            loadData();
        }
    }, [onGetUserList, setLoadingPage, userList.length]);

    React.useEffect(() => {
        const loadData = async () => {
            setLoadingPage(true);
            await onGetHistoryItems();
            setLoadingPage(false);
        };
        loadData();
    }, [onGetHistoryItems, setLoadingPage]);
    return (
        <Container>
            <Box my={2}>
                <Typography variant="h6">Export history</Typography>
            </Box>
            <Box my={3}>
                <ReportHistoryTable
                    userList={userList}
                    projectList={projectList}
                    historyItems={historyItems}
                />
            </Box>
        </Container>
    );
}
