// Core
import React from "react";

// UI
import Grid from "@material-ui/core/Grid";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import MenuItem from "@material-ui/core/MenuItem";
import MenuList from "@material-ui/core/MenuList";
import CircularProgress from "@material-ui/core/CircularProgress";

// Styles
import { StyledPopper, StyledButton } from "./style";

// Constants
import { STATUS_OPTIONS, STATUS_COLORS } from "constants/managerReports";

export default function LogStatusHandler({ status, id, onUpdateLogStatus }) {
    const [open, setOpen] = React.useState(false);
    const [isLoading, setLoadingState] = React.useState(false);

    const anchorRef = React.useRef(null);

    const handleMenuItemClick = async ({ value }) => {
        if (value !== status) {
            setOpen(false);
            setLoadingState(true);
            await onUpdateLogStatus({
                id,
                status: value,
            });
            setLoadingState(false);
        }
    };

    const handleToggle = () => {
        setOpen(prevOpen => !prevOpen);
    };

    const handleClose = event => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
            return;
        }
        setOpen(false);
    };

    const getColor = () => STATUS_COLORS[status];

    return (
        <Grid container direction="column" alignItems="center">
            <Grid item xs={12}>
                <ButtonGroup size="small" variant="contained" ref={anchorRef}>
                    <StyledButton
                        disableElevation
                        type={getColor(status)}
                        onClick={handleToggle}
                    >
                        {isLoading ? (
                            <CircularProgress size="20px" color="primary" />
                        ) : (
                            status
                        )}
                    </StyledButton>
                </ButtonGroup>
                <StyledPopper
                    open={open}
                    anchorEl={anchorRef.current}
                    transition
                    disablePortal
                >
                    {({ TransitionProps, placement }) => (
                        <Grow
                            {...TransitionProps}
                            style={{
                                transformOrigin:
                                    placement === "bottom"
                                        ? "center top"
                                        : "center bottom",
                            }}
                        >
                            <Paper>
                                <ClickAwayListener onClickAway={handleClose}>
                                    <MenuList>
                                        {STATUS_OPTIONS.map(option => (
                                            <MenuItem
                                                key={option.value}
                                                selected={
                                                    option.value === status
                                                }
                                                onClick={() =>
                                                    handleMenuItemClick(option)
                                                }
                                            >
                                                {option.label}
                                            </MenuItem>
                                        ))}
                                    </MenuList>
                                </ClickAwayListener>
                            </Paper>
                        </Grow>
                    )}
                </StyledPopper>
            </Grid>
        </Grid>
    );
}
