// Core
import React, { useState, useEffect } from "react";
import { useObserver } from "mobx-react";
// UI
import { Grid, Container, Typography, Box } from "@material-ui/core";
import ProjectsTable from "./ProjectsTable";
import CreateProjectForm from "./CreateProjectForm";
import AddButton from "components/AddButton";

// Utils
import { useStores } from "../../store";

// Styled
import { StyledGrid } from "./styles";

function useStore() {
    const { userStore, projectStore, loadersStore } = useStores();

    return useObserver(() => ({
        projectList: projectStore.projectList,
        projectTypes: projectStore.projectTypes,
        userList: userStore.userList,
        onGetProjectList: projectStore.onGetProjectList,
        onGetUserList: userStore.onGetUserList,
        onCreateProject: projectStore.onCreateProject,
        onUpdateProject: projectStore.onUpdateProject,
        onDeleteProject: projectStore.onDeleteProject,
        onGetProjectTypes: projectStore.onGetProjectTypes,
        setEditingProjectData: projectStore.setEditingProjectData,
        editingProjectData: projectStore.editingProjectData,
        setLoadingPage: loadersStore.setLoadingPage,
    }));
}

export default function Projects() {
    const [isModalVisible, setModalVisibility] = useState(false);

    const {
        projectList,
        projectTypes,
        userList,
        onCreateProject,
        onUpdateProject,
        onGetProjectList,
        onGetProjectTypes,
        onGetUserList,
        setEditingProjectData,
        editingProjectData,
        onDeleteProject,
    } = useStore();

    useEffect(() => {
        if (!userList.length) {
            onGetUserList();
        }
    }, [onGetUserList, userList.length]);

    useEffect(() => {
        if (!projectTypes.length) {
            onGetProjectTypes();
        }
    }, [onGetProjectTypes, projectTypes.length]);

    useEffect(() => {
        if (!projectList.length) {
            onGetProjectList();
        }
    }, [onGetProjectList, projectList.length]);

    useEffect(() => {
        if (editingProjectData) {
            showModal();
        }
    }, [editingProjectData]);

    const showModal = () => {
        setModalVisibility(true);
    };

    const closeModal = () => {
        setModalVisibility(false);
        setEditingProjectData();
    };

    const handleDeleteProject = id => {
        onDeleteProject(id);
        closeModal();
    };

    const onSubmitProject = async (data, id) => {
        editingProjectData
            ? await onUpdateProject(data, id)
            : await onCreateProject(data);
        closeModal();
    };

    const onEditProject = data => {
        setEditingProjectData(data);
    };

    return (
        <Container>
            <Grid container>
                <StyledGrid item xs={12}>
                    <Typography variant="h6">Available projects:</Typography>
                </StyledGrid>
                <Grid item xs>
                    <AddButton handleClick={showModal}>
                        Create project
                    </AddButton>
                </Grid>
                <Grid item xs={12}>
                    <Box pb={5}>
                        <ProjectsTable
                            projectList={projectList}
                            setEditingProjectData={onEditProject}
                        />
                    </Box>
                </Grid>
            </Grid>
            {isModalVisible && (
                <CreateProjectForm
                    open={isModalVisible}
                    onClose={closeModal}
                    userList={userList}
                    projectTypes={projectTypes}
                    handleSubmit={onSubmitProject}
                    initialData={editingProjectData}
                    handleDeleteProject={handleDeleteProject}
                />
            )}
        </Container>
    );
}
