// core
import React from "react";

// UI
import WeekSelector from "components/WeekSelector";
import DateRangePicker from "../DateRangePicker";
import ProjectSelector from "../ProjectSelector";
import UserSelector from "../UserSelector";
import { Box, Grid } from "@material-ui/core";

function FilterSelectorPanel({
    setFilter,
    projectList,
    userList,
    fromDate,
    toDate,
    weekNumber,
    selectedProjects,
    selectedUsers,
}) {
    return (
        <Box display="flex" alignItems="flex-end" flexWrap="wrap" mt={5}>
            <Grid item xs={12} sm={6}>
                <Box mx={3}>
                    <DateRangePicker
                        setFilter={setFilter}
                        fromDate={fromDate}
                        toDate={toDate}
                    />
                </Box>
            </Grid>
            <Grid item xs={12} sm={6}>
                <Box mx={3} width={"200px"}>
                    <WeekSelector
                        setWeek={value => setFilter("weekNumber", value)}
                        selectedWeek={weekNumber}
                    />
                </Box>
            </Grid>
            <Grid item xs={6}>
                <Box mx={3} mt={3}>
                    <ProjectSelector
                        projects={projectList}
                        setFilter={setFilter}
                        selectedProject={selectedProjects}
                    />
                </Box>
            </Grid>
            <Grid item xs={6}>
                <Box mx={3} mt={3}>
                    <UserSelector
                        selectedUsers={selectedUsers}
                        userList={userList}
                        setFilter={setFilter}
                    />
                </Box>
            </Grid>
        </Box>
    );
}

export default FilterSelectorPanel;
