// Core
import React from "react";
import moment from "moment";

// UI
import {
    TableContainer,
    TableHead,
    TableBody,
    Paper,
    Table,
    Box,
} from "@material-ui/core";
import TableRow from "components/TableRow";
import TableCell from "components/TableCell";

// Constants
import { TABLE_HEADERS } from "constants/developerReport";
import {
    HOUR_IN_MINUTES,
    FULL_DATE_SHORT,
    FULL_DATE_FORMAT,
} from "constants/dateFormats";

// Utils
import { getTotalTime } from "utils/developerReportUtils";

// Styles
import { StyledTableCell } from "./style";

function getTimeLabel(time) {
    let result = `${time} hours`;
    const durationMin = moment.duration(time).asMinutes();
    if (durationMin < HOUR_IN_MINUTES) {
        result = `${durationMin} minutes`;
    }
    return result;
}

export default function ({
    logList,
    handleEditLog,
    selectedDay,
    selectedWeek,
    projects,
    logTypes,
    setTotalWeekTime,
    setTotalDayTime,
}) {
    const [filteredLogs, setFilteredLogs] = React.useState([]);

    React.useEffect(() => {
        let logs = logList;
        if (selectedDay) {
            logs = logList.filter(item =>
                moment(item.date, FULL_DATE_FORMAT).isSame(
                    moment(selectedDay),
                    "day"
                )
            );
        }
        if (!selectedWeek) {
            logs = [];
        }
        setFilteredLogs(logs);
    }, [logList, selectedDay, selectedWeek]);

    React.useEffect(() => {
        setTotalWeekTime(getTotalTime(logList));
    }, [logList, setTotalWeekTime]);

    React.useEffect(() => {
        setTotalDayTime(getTotalTime(filteredLogs));
    }, [filteredLogs, setTotalDayTime]);

    const editLog = log => {
        handleEditLog(log);
    };

    const getProjectName = id => projects.find(item => item.id === id)?.name;
    const getLogTypeEnum = id => logTypes.find(item => item.id === id)?.type;
    const getType = status =>
        status === "approved"
            ? "success"
            : status === "declined"
            ? "error"
            : "default";

    return (
        <TableContainer component={Paper}>
            <Table aria-label="simple table">
                <TableHead>
                    <TableRow>
                        {TABLE_HEADERS.map(item => (
                            <TableCell key={item}>{item}</TableCell>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {filteredLogs.length ? (
                        filteredLogs.map((log, i) => (
                            <TableRow
                                hover
                                key={log.id}
                                onClick={() => editLog(log)}
                            >
                                <TableCell component="th" scope="row">
                                    {i + 1}
                                </TableCell>
                                <TableCell>
                                    {moment(log.date, FULL_DATE_FORMAT).format(
                                        FULL_DATE_SHORT
                                    )}
                                </TableCell>
                                <TableCell>
                                    {getProjectName(log.project_id)}
                                </TableCell>
                                <TableCell>
                                    {getLogTypeEnum(log.log_type_id)}
                                </TableCell>
                                <TableCell>{getTimeLabel(log.total)}</TableCell>
                                <StyledTableCell type={getType(log.status)}>
                                    {log.status}
                                </StyledTableCell>
                            </TableRow>
                        ))
                    ) : (
                        <TableRow>
                            <Box component="td" p={2}>
                                {"You don't have any logs"}
                            </Box>
                        </TableRow>
                    )}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
