// Core
import React from "react";

// UI
import {
    TableContainer,
    TableHead,
    TableBody,
    Paper,
    Table,
} from "@material-ui/core";
import TableRow from "components/TableRow";
import TableCell from "components/TableCell";

// Constants
import { TABLE_HEADERS } from "constants/projects";
import { StyledTableCell } from "components/TableCell/styles";

// Utils
import { formatTime, getDate } from "utils/projectUtils";

// Styles

export default function ({ projectList, setEditingProjectData }) {
    return (
        <TableContainer component={Paper}>
            <Table aria-label="simple table">
                <TableHead>
                    <TableRow>
                        {TABLE_HEADERS.map(item => (
                            <TableCell key={item}>{item}</TableCell>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {projectList.length ? (
                        projectList.map(project => (
                            <TableRow
                                hover
                                key={project.id}
                                onClick={() => setEditingProjectData(project)}
                            >
                                <TableCell>
                                    {getDate(project.updated_at)}
                                </TableCell>
                                <TableCell>
                                    {getDate(project.created_at)}
                                </TableCell>
                                <TableCell>{project.num_of_dev}</TableCell>
                                <TableCell>{project.name}</TableCell>
                                <TableCell>{project.type}</TableCell>
                                <StyledTableCell>
                                    {formatTime(project.total_log)}
                                </StyledTableCell>
                            </TableRow>
                        ))
                    ) : (
                        <TableRow>
                            <TableCell>{"Empty"}</TableCell>
                        </TableRow>
                    )}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
