// Core
import "moment/locale/en-sg";
import React from "react";
import MomentUtils from "@date-io/moment";

// UI
import { Box } from "@material-ui/core";
import { KeyboardDatePicker } from "@material-ui/pickers";
import { MuiPickersUtilsProvider } from "@material-ui/pickers";

// constants
import { FULL_DATE_FORMAT } from "constants/dateFormats";

// Utils
import { formatToString, getMomentInstance } from "utils/managerReportsUtils";

function DateRangePicker({ fromDate, toDate, setFilter }) {
    const changeDateFrom = date => {
        setFilter("fromDate", formatToString(date));
    };

    const changeDateTo = date => {
        setFilter("toDate", formatToString(date));
    };

    const momentFromDate = getMomentInstance(fromDate);
    const momentToDate = getMomentInstance(toDate);

    return (
        <MuiPickersUtilsProvider utils={MomentUtils} locale={"en-sg"}>
            <Box display="flex">
                <Box mr={1}>
                    <KeyboardDatePicker
                        value={momentFromDate}
                        label="From date"
                        maxDate={toDate ? momentToDate : false}
                        onChange={changeDateFrom}
                        format={FULL_DATE_FORMAT}
                    />
                </Box>
                <Box mr={1}>
                    <KeyboardDatePicker
                        label="To date"
                        value={momentToDate}
                        minDate={fromDate ? momentFromDate : false}
                        maxDate={getMomentInstance(new Date())}
                        onChange={changeDateTo}
                        format={FULL_DATE_FORMAT}
                    />
                </Box>
            </Box>
        </MuiPickersUtilsProvider>
    );
}

export default DateRangePicker;
