import moment from "moment";

import { DEFAULT_FORM_VALUES } from "constants/projects";
import { HOUR_IN_MINUTES } from "constants/dateFormats";

export const getInitialFormValues = initialData => {
    if (!initialData) {
        return {
            ...DEFAULT_FORM_VALUES,
        };
    }
    return Object.keys(DEFAULT_FORM_VALUES).reduce((acc, key) => {
        acc[key] = initialData[key];
        return acc;
    }, {});
};

export const formatTime = time => {
    const durationMin = moment.duration(time).asMinutes();
    return durationMin < HOUR_IN_MINUTES
        ? `${durationMin}m`
        : `${moment.duration(time).hours()}h ${moment
              .duration(time)
              .minutes()}m`;
};

export const getDate = date => moment(date).format("DD MMMM YYYY");

export const getTime = date => moment(date).format("hh:mm");
