// Core
import React, { useEffect, useState, useCallback } from "react";
import { useObserver } from "mobx-react";
// UI
import FilterSelectorPanel from "./FilterSelectorPanel";
import { Container, Box } from "@material-ui/core";
import LogTableReports from "./LogReportTable";

// Store
import { useStores } from "store";

// Utils
import {
    getWeekNumber,
    getFromDate,
    getToDate,
    checkIfWeekSelected,
} from "utils/managerReportsUtils";

function useStore() {
    const store = useStores();

    return useObserver(() => ({
        logs: store.logStore.logs,
        projectList: store.projectStore.projectList,
        userList: store.userStore.userList,
        onGetUserList: store.userStore.onGetUserList,
        onGetLogs: store.logStore.onGetLogs,
        onGetProjectList: store.projectStore.onGetProjectList,
        onUpdateLogStatus: store.logStore.onUpdateLogStatus,
        onDownload: store.exportStore.onDownload,
    }));
}

export default function ManagerReports() {
    const {
        logs,
        projectList,
        onGetProjectList,
        onGetLogs,
        userList,
        onGetUserList,
        onUpdateLogStatus,
        onDownload,
    } = useStore();

    const currentWeek = getWeekNumber(new Date());

    const [weekNumber, setWeekNumber] = useState(currentWeek);
    const [fromDate, setFromDate] = useState(getFromDate(currentWeek));
    const [toDate, setToDate] = useState(getToDate(currentWeek));
    const [selectedProjects, setSelectedProjects] = useState([]);
    const [selectedUsers, setSelectedUsers] = useState([]);

    useEffect(() => {
        if (weekNumber) {
            setFromDate(getFromDate(weekNumber));
            setToDate(getToDate(weekNumber));
        }
    }, [weekNumber]);

    useEffect(() => {
        if (checkIfWeekSelected(fromDate, toDate)) {
            setWeekNumber(getWeekNumber(fromDate));
            return;
        }
        setWeekNumber(null);
    }, [fromDate, toDate]);

    const onFilterChange = useCallback((type, value) => {
        const setValueTypes = {
            fromDate: () => setFromDate(value),
            toDate: () => setToDate(value),
            weekNumber: () => setWeekNumber(value),
            selectedProjects: () => setSelectedProjects(value),
            selectedUsers: () => setSelectedUsers(value),
        };

        setValueTypes[type]();
    }, []);

    useEffect(() => {
        if (!projectList.length) {
            onGetProjectList();
        }
        if (!userList.length) {
            onGetUserList();
        }
    }, [onGetProjectList, onGetUserList, projectList, userList]);

    const onDownloadFile = () => {
        onDownload({
            from_date: fromDate,
            to_date: toDate,
            week: weekNumber,
            users: selectedUsers.map(item => item.id),
            projects: selectedProjects.map(item => item.id),
        });
    };

    return (
        <Container>
            <FilterSelectorPanel
                fromDate={fromDate}
                toDate={toDate}
                weekNumber={weekNumber}
                selectedProjects={selectedProjects}
                selectedUsers={selectedUsers}
                setFilter={onFilterChange}
                projectList={projectList}
                logs={logs}
                userList={userList}
            />
            <Box mt={4}>
                <LogTableReports
                    onGetLogs={onGetLogs}
                    fromDate={fromDate}
                    toDate={toDate}
                    logList={logs}
                    userList={userList}
                    projectList={projectList}
                    onUpdateLogStatus={onUpdateLogStatus}
                    selectedProjects={selectedProjects}
                    selectedUsers={selectedUsers}
                    onDownloadFile={onDownloadFile}
                />
            </Box>
        </Container>
    );
}
