// Core
import { observable, action, decorate } from "mobx";
import userService from "../services/api/UserService";

// Constants
import { DEFAULT_PAGE, DEFAULT_PER_PAGE } from "constants/pagination";

const fetchStrategy = ({ body, id }) => ({
    delete: () => userService.deleteUser(id),
    update: () => userService.updateUser(body, id),
    create: () => userService.createUser(body),
});

const updateUserListStrategy = {
    update: (dataArray, newItem) =>
        dataArray.map(item => (item.id === newItem.id ? newItem : item)),
    delete: (dataArray, newItem) =>
        dataArray.map(item => (item.id === newItem.id ? newItem : item)),
    create: (userList, item) => [...userList, item],
};

class UserStore {
    constructor(rootStore) {
        this.rootStore = rootStore;

        this.userList = [];
        this.editingUserData = undefined;
        this.page = DEFAULT_PAGE;
        this.perPage = DEFAULT_PER_PAGE;
    }

    showNotification = (type, message) => {
        this.rootStore.notificationStore.showNotification({
            message,
            type,
        });
    };

    setIsLoading(value) {
        this.rootStore.loadersStore.setLoadingPage(value);
    }

    onGetUserList = async () => {
        try {
            this.setIsLoading(true);
            const response = await userService.getUserList({
                page: this.page,
                per_page: this.perPage,
            });
            this.userList = response?.data?.data;
        } catch (err) {
            this.showNotification("error", err?.response?.data?.message);
            throw err;
        } finally {
            this.setIsLoading(false);
        }
    };

    handleFetchUserData = async (actionType, data) => {
        try {
            this.setIsLoading(true);
            const userData = await fetchStrategy(data)[actionType]();
            this.userList = updateUserListStrategy[actionType](
                this.userList,
                userData?.data?.data
            );
            this.showNotification("success", "User saved");
        } catch (error) {
            console.log("error?.response: ", error?.response?.data?.message);
            this.showNotification("error", error?.response?.data?.message);
            throw error;
        } finally {
            this.setIsLoading(false);
        }
    };

    onCreateUser = body => this.handleFetchUserData("create", { body });

    onUpdateUser = (body, id) =>
        this.handleFetchUserData("update", { body, id });

    onDeleteUser = id => this.handleFetchUserData("delete", { id });

    set userList(userList) {
        this.userList = userList;
    }

    setEditingUserData = user => {
        this.editingUserData = user;
    };
}

decorate(UserStore, {
    // state
    userList: observable,
    editingUserData: observable,

    // actions
    setEditingUserData: action,
    onGetUserList: action,
    onCreateUser: action,
    onUpdateUser: action,
    onDeleteUser: action,
});

export default UserStore;
